import { Component, OnInit } from '@angular/core';
import { RequestService } from 'src/app/api/request.service';
import { Model } from 'src/app/api/models/model';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ActivatedRoute } from '@angular/router';

import html2canvas from 'html2canvas';
import { image } from 'html2canvas/dist/types/css/types/image';


@Component({
  selector: 'app-banner',
  templateUrl: './banner.page.html',
  styleUrls: ['./banner.page.scss'],
})
export class BannerPage implements OnInit {

  public gameModel : Model;

  public game : any;
  public id : any;
  public image2 = 'a';


  constructor(
    public route : ActivatedRoute,
    public requestService : RequestService,
    public dialogService : DialogService
  ) { 
    this.gameModel = new Model('Game',requestService);
    this.id = this.route.snapshot.paramMap.get('id');

  }

  ngOnInit() {
    this.load();

  }

  async load(event = null){
    await this.dialogService.loadingProgres('Cargando equipo');

    this.gameModel.api_functionModel(this.id,'pageBanner').subscribe(
      response => {
        this.dialogService.dimissLoaging();
        if(response['status'] = 'success'){
          this.game = response['data'];

          setTimeout(this.createImage,1000);
        }
      }
    )

  }

  createImage(){
    console.log('Create banner')
    var element :any  = document.getElementById('banner');
    console.log('element' , element.width)
    html2canvas(element).then((canvas) =>
    {
      let MAX_WIDTH = 1000;

      if(MAX_WIDTH > element.offsetWidth)
      MAX_WIDTH = element.offsetWidth

      const scaleSize = MAX_WIDTH / element.offsetWidth;

      canvas.width = MAX_WIDTH;
      canvas.height = element.offsetWidth * scaleSize;

      canvas.getContext('2d');
      this.image2 = canvas.toDataURL('image/png');

      //var destination = document.getElementById('destination');
      //destination.appendChild(canvas);
      element.appendChild(canvas)

    });
/*
    //const onCapture = (id) =>{
      htmlToImage.toPng(document.getElementById('test'))
       .then((dataUrl) => {
         //saveAs(dataUrl, 'my-node.png');
         console.log(dataUrl)
         this.image2 = dataUrl;
         var img = document.createElement("img");
          img.src = dataUrl;

         element.appendChild(img);
       });
  */   
  }



}
